.navigation{display:flex;justify-content:space-between;align-items:center;margin-bottom:20px;position:-webkit-sticky;position:sticky;top:0;padding:10px 0;background:#faf2f0}.navigation a{border-bottom:0}@media (min-width: 500px){.navigation{justify-content:flex-end}.navigation a{margin-right:20px}}.navigation img{width:25px}.navigation .primary-button{font-size:14px}@media (min-width: 500px){.navigation .primary-button{font-size:16px}}

.link{color:#AB5232;text-decoration:none;border-bottom:2px solid #e4dee3}.link:hover{opacity:.7;transition:ease .3s}

.button{border-radius:6px;padding:4px 8px;color:#AB5232;border:0;font-family:"IBM Plex Mono",monospace;border:2px solid #AB5232;background:#faf2f0;font-size:12px}@media (min-width: 768px){.button:hover{background:#AB5232;color:#faf2f0;transition:ease .3s}}@media (max-width: 500px){.button:focus{outline-style:dotted;outline-width:medium;outline-offset:5px;outline-color:#AB5232}}

@media (min-width: 768px){.opening-text{margin-bottom:70px}}.opening-text h1,.opening-text code{font-weight:300}.opening-text h1{color:#233172;font-family:"Playfair Display",serif;margin:5px 0 20px 0}.opening-text .bio{color:#6b6768}

.essays{margin-bottom:50px}.essays .loading{color:#6b6768;font-size:20px}.essays .title-link{color:#AB5232;text-decoration:none;border-bottom:2px solid #e4dee3}.essays .title-link:hover{opacity:.7;transition:ease .3s}.essays .time,.essays .content{margin:10px 0}.essays .content{line-height:22px}.essays .essay{margin-bottom:30px}@media (min-width: 768px){.essays .essay{margin-bottom:50px}}

.projects{display:flex;overflow:scroll;padding-bottom:10px}@media (max-width: 768px){.projects::-webkit-scrollbar{width:0;height:3px;background:#e4dee3}.projects::-webkit-scrollbar-thumb{background:#6b6768;outline:1px solid #6b6768;border-radius:6px}}@media (min-width: 768px){.projects{display:inline;display:initial;overflow:visible;overflow:initial;padding-bottom:0}}.projects .project{margin-right:30px}@media (min-width: 375px){.projects .project{margin-right:40px}}@media (min-width: 768px){.projects .project{margin-bottom:60px;margin-right:80px}}.projects .heading{display:flex;align-items:flex-start}.projects .heading img{width:80px;margin-right:4px}@media (min-width: 768px){.projects .heading img{width:100px;margin-right:16px}}.projects .heading h3 a{font-size:16px}@media (min-width: 768px){.projects .heading h3 a{font-size:20px}}.projects .heading .timeline,.projects .heading .tags{margin:5px 0}.projects .heading .tags{font-style:italic}

.home-page .opening-text .emoji{font-size:60px}.home-page .opening-text h1{font-size:40px}@media (min-width: 500px){.home-page .opening-text h1{font-size:60px}}.home-page .opening-text .bio{font-size:20px}.home-page .skeleton h2{font-size:40px;font-weight:400;color:#233172;font-family:"Playfair Display",serif;margin-top:0;margin-bottom:16px}@media (min-width: 768px){.home-page .skeleton{display:flex}}.home-page .skeleton .left{margin:40px 0}@media (min-width: 768px){.home-page .skeleton .left{flex:2 1;margin:0px}}@media (min-width: 768px){.home-page .skeleton .right{flex:3 1}}.home-page .skeleton .essays h3,.home-page .skeleton .projects h3{font-size:20px}.home-page .skeleton .essays h3,.home-page .skeleton .essays h4,.home-page .skeleton .projects h3,.home-page .skeleton .projects h4{margin:0 0 10px 0;text-transform:uppercase}.home-page .skeleton .essays .time,.home-page .skeleton .essays .tags,.home-page .skeleton .essays .timeline,.home-page .skeleton .projects .time,.home-page .skeleton .projects .tags,.home-page .skeleton .projects .timeline{color:#6b6768}.home-page .skeleton .essays .content,.home-page .skeleton .essays .pitch,.home-page .skeleton .projects .content,.home-page .skeleton .projects .pitch{color:#242424}.home-page .skeleton .essays .time,.home-page .skeleton .essays h4,.home-page .skeleton .essays .tags,.home-page .skeleton .essays .timeline,.home-page .skeleton .projects .time,.home-page .skeleton .projects h4,.home-page .skeleton .projects .tags,.home-page .skeleton .projects .timeline{font-size:14px}

.essay-container{max-width:700px;margin:0 auto}.essay-container .title,.essay-container .thesis{color:#233172}.essay-container .details{color:#6b6768;line-height:6px;padding-bottom:14px;border-bottom:1px solid #e4dee3}.essay-container .content{color:#242424}.essay-container .content .images{max-width:100%}@media (min-width: 768px){.essay-container .content .images{max-width:500px;display:block;margin:0 auto}}.essay-container .title{font-family:"Playfair Display",serif;font-size:40px}.essay-container .content{line-height:26px}.essay-container .thesis{font-size:20px;font-weight:bold}.essay-container .quote{border-left:3px solid;margin-left:20px;padding-left:20px;font-style:italic}

.essay-page .opening-text .emoji{font-size:40px}.essay-page .opening-text h1{font-size:26px}.essay-page .opening-text .bio{font-size:18px}.essay-page .back-link{color:#AB5232;text-decoration:none;border-bottom:2px solid #e4dee3}.essay-page .back-link:hover{opacity:.7;transition:ease .3s}

